import { NodeElement, NodeParameter, ParameterTypes } from "../Node.js";

export default class Translate extends NodeElement {

	static get parameters() { return [
		new NodeParameter("x"),
		new NodeParameter("y"),
		new NodeParameter("z"),
		new NodeParameter("out", {
			input: false,
			editable: false,
			type: ParameterTypes.VEC3
		})
	]}

	constructor() {
		super();

		this.name = "Translate";

		this.parameters.out.modifier = (value) => {
			return [
				this.parameters.x.value,
				this.parameters.y.value,
				this.parameters.z.value,
			];
		}
	}
}
