import { NodeElement, NodeParameter, ParameterTypes } from "../Node.js";

export default class Emitter extends NodeElement {

	get x() { return 0; }
	get y() { return 0; }
	get z() { return 0; }

	get rotX() { return 0; }
	get rotY() { return 0; }
	get rotZ() { return 0; }

	get shader() {
		return this.parameters.shader.value;
	}

	static get parameters() { return [
		new NodeParameter("position", { type: ParameterTypes.VEC3, default: [0,0,0], editable: false }),
		new NodeParameter("density", {default: 50}),
		new NodeParameter("rate", {default: 100}),
		new NodeParameter("death", {default: 10}),
		new NodeParameter("speed", {default: 10}),
		new NodeParameter("color", { type: ParameterTypes.COLOR, default: [100, 100, 100], editable: false }),
		new NodeParameter("shader", { type: ParameterTypes.SHADER, editable: false }),
		new NodeParameter("wind", { type: ParameterTypes.VEC3, default: [0,0,0], editable: false }),
	]}

	get buffer() {
		const verts = [];

		for (let i = 0; i < this.particles.length; i++) {
			verts.push(...this.particles[i].position, ...this.particles[i].color)
		}

		if(verts.length > 0) {
			return {
				type: "POINTS",
				elements: 6, 
				vertecies: new Float32Array(verts),
				attributes: [
					{ size: 3, attribute: "aPosition" },
					{ size: 3, attribute: "aColor" }
				]
			};
		}

		return null;
	}

	constructor() {
		super();

		this.name = "Emitter";
		
		this.particles = [];
		this.updateRate = 144;
		this.volume = 10000;

		const anim = () => {
			setTimeout(anim, 1000 / this.updateRate)
			this.update();
		}
		anim();

		this.lastEmit = 0;
	}

	emitt(count) {
		for(let i = 0; i < count; i++) {
			this.particles.push(
				new Particle(
					this.parameters.position.value,
					this.parameters.speed.value,
					this.parameters.color.value,
				)
			);
		}
		this.lastEmit = performance.now();
	}

	update() {
		for(let p of this.particles) {

			const wind = this.parameters.wind.value;
			vec3.add(p.velocity, p.velocity, wind);

			const friciton = vec3.create();
			friciton[0] = 0.99;
			friciton[1] = 0.99;
			friciton[2] = 0.99;

			vec3.mul(p.velocity, p.velocity, friciton);

			p.position[0] += p.velocity[0];
			p.position[1] += p.velocity[1];
			p.position[2] += p.velocity[2];

			if (Math.max(Math.abs(p.velocity[0]), Math.abs(p.velocity[1]), Math.abs(p.velocity[2])) < this.parameters.death.value ||
				Math.abs(p.position[0]) > this.volume || Math.abs(p.position[1]) > this.volume || Math.abs(p.position[2]) > this.volume) {
				this.particles.splice(this.particles.indexOf(p), 1)
			}
		}

		const delta = performance.now() - this.lastEmit;
		if(delta > this.parameters.rate.value) {
			this.emitt(this.parameters.density.value);
		}
	}
}

class Particle {
	constructor(pos, speed = 0, color = [1, 1, 1]) {
		this.position = pos;
		this.velocity = vec3.create();
		vec3.random(this.velocity, speed + Math.random() * (speed*2));
		this.color = color;
	}
}
