import { NodeElement, NodeParameter, ParameterTypes } from "../Node.js";

export default class Plane extends NodeElement {

	get x() { return this.parameters.position.value[0]; }
	get y() { return this.parameters.position.value[1]; }
	get z() { return this.parameters.position.value[2]; }

	get rotX() { return this.parameters.rotation.value[0]; }
	get rotY() { return this.parameters.rotation.value[1]; }
	get rotZ() { return this.parameters.rotation.value[2]; }

	get shader() {
		return this.parameters.shader.value;
	}

	static get parameters() { return [
		new NodeParameter("position", { type: ParameterTypes.VEC3, default: [0,0,0], editable: false }),
		new NodeParameter("rotation", { type: ParameterTypes.VEC3, default: [0,0,0], editable: false }),
		new NodeParameter("shader", { type: ParameterTypes.SHADER, editable: false }),
	]}

	constructor() {
		super();

		this.name = "Plane";
		this.buffer = planebufferdata();
	}
}

function planebufferdata(w = 300, h = 300, s = 1) {
	return {
		type: "TRIANGLES",
		elements: 6, 
		vertecies: new Float32Array([
		// 	x y z  u  v  //
			s * w, 0, s * h, 1, 1, 1,
			s * w, 0, -s * h,  1, 0, 1,
			-s * w, 0, -s * h,  0, 0, 1,
			s * w, 0, s * h,  1, 1, 1,
			-s * w, 0, -s * h, 0, 0, 1,
			-s * w, 0, s * h,  0, 1, 1,
		]),
		attributes: [
			{ size: 3, attribute: "aPosition" },
			{ size: 3, attribute: "aColor" }
		]
	}
}