import { NodeElement, NodeParameter } from "../Node.js";

export default class TimerNode extends NodeElement {

	static get parameters() { return [
		new NodeParameter("Output")
	]}

	constructor() {
		super();

		this.name = "Timer";

		setInterval(() => {
			this.parameters.output._input.value = this.parameters.output.value+1;
		}, 10)
	}
}