if(!window.shortcuts) {
	window.shortcuts = new Map();
}

export class Shortcuts {
	static map() {
		return window.shortcuts;
	}
}

export default class Component extends HTMLElement {

	static get style() {
		return ``;
	}

	static get template() {
		return `<slot></slot>`;
	}

	constructor() {
		super();

		this.attachShadow({mode: "open"});
		this.shadowRoot.innerHTML = this.constructor.style;
		this.shadowRoot.innerHTML += this.constructor.template;

		this.shortcutMap = Shortcuts.map();
		
		this.tabIndex = 0;
		this.addEventListener("keydown", e => {
			this.executeShortcut(e);
		});
	}

	mapShortcut(args = {
		key: null,
		description: "",
		action: null,
	}) {
		this.shortcutMap.set(args.key.toLowerCase(), {
			key: args.key,
			exec: args.action,
			description: args.description,
		})
	}

	executeShortcut(e) {
		if (this.shortcutMap.has(e.key.toLowerCase()))
			this.shortcutMap.get(e.key.toLowerCase()).exec(e);
	}
}