import { NodeElement, NodeParameter, ParameterTypes } from "../Node.js";

export default class Color extends NodeElement {

	static get parameters() { return [
		new NodeParameter("r"),
		new NodeParameter("g"),
		new NodeParameter("b"),
		new NodeParameter("out", {
			input: false,
			editable: false,
			type: ParameterTypes.COLOR
		})
	]}

	constructor() {
		super();

		this.name = "Color";
		
		this.parameters.out.modifier = (value) => {
			return [
				this.parameters.r.value / 100,
				this.parameters.g.value / 100,
				this.parameters.b.value / 100,
			];
		}
	}
}
