import { NodeElement, NodeParameter } from "../Node.js";

export default class MultiplyNode extends NodeElement {

	static get parameters() { return [
		new NodeParameter("Ratio", { default: 2 }),
		new NodeParameter("Output")
	]}

	constructor() {
		super();

		this.name = "Multiply";
		
		this.parameters.output.modifier = (value) => {
			return value * this.parameters.ratio.value;
		}
	}
}
