import Color from './nodes/Color.js';
import Shader from './nodes/Shader.js';
import Translate from './nodes/Translate.js';
import ValueNode from './nodes/ValueNode.js';
import TimerNode from './nodes/TimerNode.js';
import Time from './nodes/Time.js';
import MultiplyNode from './nodes/MultiplyNode.js';
import Sin from './nodes/Sin.js';
import Cos from './nodes/Cos.js';
import Sum from './nodes/Sum.js';
import Plane from './nodes/Plane.js';
import Cube from './nodes/Cube.js';
import Emitter from './nodes/Emitter.js';

window.ne = {

    nodeTypes: {
        "Multiply": MultiplyNode,
        Sin, 
        Cos, 
        Sum, 
        Translate, 
        Emitter, 
        Cube, 
        Plane, 
        Color, 
        Shader,
        "Timer": TimerNode,
        "Value": ValueNode,
        Time,
    },

    loadSavestate: jsonObj => {

        const json = {
            editor: jsonObj.editor || [],
            timeline: jsonObj.timeline || {
                keyframes: [
                    [], []
                ]
            }
        }
    
        loadEditor(json.editor);
        loadTimeline(json.timeline);
    
    
        function loadTimeline(json) {
            const timeline = document.querySelector("ne-timeline");
            timeline.update(json.keyframes);
        }
        
        function loadEditor(json) {
            editor.zoom = 1;
    
            for(let node of editor.nodes) {
                editor.remove(node);
            }
    
            for(let nodeData of json) {
                const node = editor.createNode(
                    ne.nodeTypes[nodeData.type],
                    nodeData.position.x,
                    nodeData.position.y
                );
                for(let para in node.parameters) {
                    if(nodeData.parameters[para]) {
                        node.parameters[para].value = nodeData.parameters[para].value;
                    } else {
                        console.log("missing parameter", para);
                    }
                }
            }
    
            // make connections
            const nodes = [...editor.nodes];
    
            nodes.forEach((node, n) => {
                for(let para in node.parameters) {
                    if(json[n].parameters[para]) {
                        const input = json[n].parameters[para].input;
                        if(input) {
                            const inputNode = nodes[input.node];
                            if(inputNode) {
                                const inputParameter = inputNode.parameters[input.parameter];
                                node.parameters[para].connect(inputParameter);
                            }
                        }
                    }
                }
            })
    
            editor.update();
    
            return true;
        }
    
        console.log("Savestate loaded");
    },

    saveSavestate: () => {
        // save timeline
        function saveTimeline() {
            const timeline = document.querySelector("ne-timeline");
            return {
                keyframes: timeline.keyframes,
            }
        }
    
        // save editor
        function saveEditor() {
            let result = [];
            for(let node of editor.nodes) {
                const n = {
                    type: node.title,
                    position: {
                        x: node.position.x,
                        y: node.position.y,
                    },
                    parameters: {}
                };
                
                for(let para in node.parameters) {
                    n.parameters[para] = {
                        value: node.parameters[para].value,
                        input: null
                    }
                    if(node.parameters[para].input) {
                        n.parameters[para].input = {
                            node: [...editor.nodes].indexOf(node.parameters[para].input.node),
                            parameter: node.parameters[para].input.title.toLowerCase(),
                        }
                    }
                }
    
                result.push(n);
            }
            return result;
        }
    
        const timelineSave = saveTimeline();
    
        const saveState = {
            editor: saveEditor() || [],
            timeline: {
                keyframes: timelineSave.keyframes || [[], []],
            }
        }
    
        console.log(JSON.stringify(saveState));
    
        return saveState;
    }
}