import { NodeElement, NodeParameter } from "../Node.js";

export default class Sum extends NodeElement {

	static get parameters() { return [
		new NodeParameter("Input1"),
		new NodeParameter("Input2"),
		new NodeParameter("Output", {
			input: false, 
			editable: false
		})
	]}

	constructor() {
		super();

		this.name = "Sum";

		this.parameters.output.modifier = (value) => {
			return this.parameters.input1.value + this.parameters.input2.value;
		}
	}
}
