import Component from "./Component.js";

export default class Tabmenu extends Component {

    static get style() {
        return `
            <style>
                .tabmenu {
                    position: absolute;
                    left: 20px;
                    top: 20px;
                }
                
                .tabmenu input {
                    padding: 8px 12px;
                    background: #1c1c1c;
                    color: #eee;
                    border: 1px solid #000000;
                    border-radius: 4px;
                    outline: none;
                }
                
                .tabmenu .options {
                    display: flex;
                    flex-direction: column;
                    overflow: hidden;
                    border-radius: 3px;
                }
                
                .tabmenu .tabmenu-item {
                    background: #1c1c1c;
                    padding: 7px 10px;
                    border-bottom: 1px solid #101010;
                    font-family: sans-serif;
                    color: #bdbdbd;
                    font-size: 14px;
                }
                
                .tabmenu .tabmenu-item:hover,
                .tabmenu .tabmenu-item[active] {
                    background: #1c1c1c;
                    background-image: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1));
                }
            </style>
        `;
    }

    constructor(container, options, editor) {
        super();

        this.container = container;

        this.editor = editor;
        this.options = options;
        this.maxresults = 8;

        this.position = [0,0];

        this.container.addEventListener("mousemove", e => {
            const bounds = this.container.getBoundingClientRect();
            this.position[0] = e.clientX - bounds.left;
            this.position[1] = e.clientY - bounds.top;
        })

        this.isopen = false;
        this.selected = 0;
        this.currentResults = [];

        // shortcuts
        this.mapShortcut({
            key: "Tab",
            description: "Open tab menu",
            action: (e) => {
                if(!this.isopen) {
                    e.preventDefault();
                    this.open(this.container, this.position[0], this.position[1]);
                }
            }
        });
    }

    open(container, x, y) {
        const ele = this.render();
        this.element = ele;
        container.appendChild(this.element);

        if(x && y) {
            ele.style.left = x + "px";
            ele.style.top = y + "px";
            ele.style.position = "absolute";
        }

        ele.querySelector("input").oninput = e => {
            this.search(e.target.value, Object.keys(this.options));
            this.selected = 0;
            this.renderItems(ele.querySelector(".options"), this.currentResults);
        }

        ele.querySelector("input").onkeydown = e => {
            if(e.key == "Escape") {
                this.close();
            } else if(e.key == "Enter") {
                this.submit();
            } 
            if(e.key == "ArrowUp") {
                e.preventDefault();
                this.selected--;
            } else if(e.key == "ArrowDown") {
                e.preventDefault();
                this.selected++;
            }

            this.renderItems(ele.querySelector(".options"), this.currentResults);
        }

        this.isopen = true;
    }

    close() {
        if(this.element && this.isopen) {
            this.isopen = false;
            this.element.parentElement.focus();
            this.element.remove();
            this.element = null;
        }
    }

    submit() {
        if(this.isopen && this.currentResults[this.selected]) {
            const node = this.options[this.currentResults[this.selected]];
            this.editor.createNode(node, this.position[0], this.position[1]);
            this.close();
        }
    }

    search(str, options) {
        let result = [];

        if(str != "" && str.length >= 1) {
            for(let opt of options) {
                if(opt.toLowerCase().match(str.toLowerCase())) {
                    result.push(opt);
                }
                if(result.length > this.maxresults) {
                    break;
                }
            }
        }

        this.currentResults = result;
        return result;
    }

    renderItems(container, items = []) {
        container.innerHTML = "";

        for(let item of items) {
            const ele = document.createElement("span");
            ele.className = "tabmenu-item";
            ele.innerHTML = item;
            container.appendChild(ele);

            ele.onmousedown = () => {
                this.selected = items.indexOf(item);
                this.submit();
            }

            this.selected = this.selected % items.length;
            if(items.indexOf(item) == this.selected) {
                ele.setAttribute("active", "");
            }
        }
    }

    render(x, y) {
        const ele = document.createElement("div");
        ele.className = "tabmenu";

        const input = document.createElement("input");
        input.placeholder = "Search";
        input.tabIndex = 0;
        
        const list = document.createElement("div");
        list.className = "options";

        if(x && y) {
            ele.style.position = "absolute";
            ele.style.left = x + "px";
            ele.style.top = y + "px";
        }

        ele.innerHTML = this.constructor.style;
        ele.appendChild(input);
        ele.appendChild(list);
        this.shadowRoot.appendChild(ele);

        input.onblur = () => {
            this.close();
        }
        setTimeout(() => {
            input.focus();
        }, 10);

        return ele;
    }

}

customElements.define('tab-menu', Tabmenu);
